import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';

import {
  Container,
  WrapperTop,
  WrapperImport,
  IconCsv,
  WrapperTitleQuotes,
  WrapperExport,
  IconPrint,
  PainelHeader,
  ContentPainelHeader,
  IconHand,
  WrapperIcons,
  Title,
  Button,
  TagIcon,
  TitleInfo,
  TextInfo,
  ArrowUpIcon,
  ArrowDownIcon,
  // LinkNew,
  Import,
  IconSave,
  WrapperSave,
  WrapperPanel
} from './styles';

import {
  RowPanel,
  LeftPanel,
  CenterPanel,
  CenterPanelRow,
  CenterPanelRowContent,
  RightPanel
} from '../../components/RowPanel';

interface ParamTypes {
  quoteId: string;
}

interface IQuote {
  id: string;
  amount: number;
  product: string;
}

const mokkaData = [
  { id: '0001', amount: 10, product: 'placa teste 1' },
  { id: '0002', amount: 5, product: 'placa teste 2' }
];

const QuotesDetail: React.FC = () => {
  const { quoteId } = useParams<ParamTypes>();
  const [quote, setQuote] = useState<IQuote[]>([]);
  const [active, setActive] = useState(true);

  useEffect(() => {
    setQuote(mokkaData);
  }, []);

  return (
    <Container>
      <WrapperTop>
        <WrapperImport>
          <span>Importar</span>
          <Import>
            <input id="file-upload" type="file" required />
            <label htmlFor="file-upload">
              <IconCsv />
            </label>
            <WrapperSave className="import">
              <IconSave />
              <span>Salvar</span>
            </WrapperSave>
          </Import>
        </WrapperImport>
        <WrapperTitleQuotes>
          <IconHand />
          <Title>cotação</Title>
          <span>{quoteId}</span>
        </WrapperTitleQuotes>
        <WrapperExport>
          <span>Exportar</span>
          <WrapperIcons>
            <Button>
              <IconCsv />
            </Button>
            <Button>
              <IconPrint />
            </Button>
          </WrapperIcons>
        </WrapperExport>
      </WrapperTop>
      <PainelHeader>
        <ContentPainelHeader>conteudo de cima</ContentPainelHeader>
      </PainelHeader>

      <WrapperPanel>
        {quote.map(q => (
          // <LinkNew key={q.id}>
          <RowPanel key={q.id}>
            <LeftPanel>
              <TagIcon />
            </LeftPanel>
            <CenterPanel>
              <CenterPanelRow>
                <CenterPanelRowContent>
                  <TitleInfo>cód</TitleInfo>
                  <TextInfo>{q.id}</TextInfo>
                </CenterPanelRowContent>
                <CenterPanelRowContent>
                  <TitleInfo>Quantidade</TitleInfo>
                  <TextInfo>{q.amount}</TextInfo>
                </CenterPanelRowContent>
              </CenterPanelRow>
              <CenterPanelRow>
                <CenterPanelRowContent>
                  <TitleInfo>Produto</TitleInfo>
                  <TextInfo>{q.product}</TextInfo>
                </CenterPanelRowContent>
              </CenterPanelRow>
            </CenterPanel>
            <RightPanel>
              <ArrowUpIcon className={!active ? 'active' : ''} />
              <ArrowDownIcon className={active ? 'active' : ''} />
            </RightPanel>
          </RowPanel>
          // </LinkNew>
        ))}
      </WrapperPanel>
    </Container>
  );
};
export default QuotesDetail;
